import { useState } from 'react';
import { ClassAnalysis, ClassObjective } from '@/types/classAnalysis';
import { useToast } from '@/hooks/use-toast';

export const useAnalysis = () => {
  const { toast } = useToast();
  
  const [classAnalysis, setClassAnalysis] = useState<ClassAnalysis>({
    summary: '',
    rubricEvaluation: {
      domainKnowledge: 0,
      teachingMethodology: 0,
      studentEngagement: 0,
      classroomManagement: 0,
      communicationSkills: 0,
      comments: []
    },
    structuredObservation: {
      planningAndPreparation: [],
      instructionalDelivery: [],
      classroomEnvironment: [],
      professionalResponsibilities: []
    },
    feedback360: {
      selfEvaluation: [],
      studentPerspective: [],
      peerObservations: [],
      improvementAreas: []
    },
    teachingPortfolio: {
      strengths: [],
      innovativePractices: [],
      studentLearningEvidence: [],
      reflectiveInsights: []
    },
    ecdfModel: {
      domainExpertise: '',
      pedagogicalKnowledge: '',
      contextualKnowledge: '',
      professionalDevelopment: '',
      score: 0
    },
    studentParticipation: '',
    professorPerformance: '',
    voiceAnalysis: {
      totalSpeakers: 0,
      professorSpeechTime: 0,
      studentSpeechTime: 0,
      questionCount: 0,
      interactionCount: 0
    },
    contentAnalysis: {
      topicsDiscussed: [],
      conceptsExplained: [],
      examplesUsed: [],
      keywordFrequency: {}
    },
    classObjective: {
      mainObjective: '',
      specificObjectives: [],
      achievementStatus: 'not_achieved',
      evidence: [],
      recommendations: []
    }
  });
  const [isAnalyzing, setIsAnalyzing] = useState(false);

  const analyzeTranscriptContent = (text: string) => {
    const words = text.toLowerCase().split(/\s+/).filter(word => word.length > 0);
    const wordCount = words.length;
    
    // Anlisis bsico de contenido
    const analysis = {
      wordCount,
      mainWords: [] as [string, number][],
      topicsDiscussed: [] as string[],
      examplesUsed: [] as string[],
      keywordFrequency: {} as Record<string, number>,
      explanationCount: 0,
      questionCount: 0,
      professorIndicators: 0,
      studentIndicators: 0
    };

    // Detectar preguntas
    const questionPatterns = [
      /\?/g,
      /\b(qu|quien|cuando|donde|por qu|como|cual|cuanto|cuantos|cuantas)\b/gi,
      /\b(explique|describe|define|analiza|compara|contrasta|evala|justifica)\b/gi
    ];
    
    questionPatterns.forEach(pattern => {
      const matches = text.match(pattern);
      if (matches) {
        analysis.questionCount += matches.length;
      }
    });

    // Detectar explicaciones
    const explanationPatterns = [
      /\b(porque|debido a|ya que|puesto que|dado que)\b/gi,
      /\b(es decir|en otras palabras|esto significa que|lo que implica)\b/gi,
      /\b(por ejemplo|como ejemplo|un caso|una instancia)\b/gi
    ];
    
    explanationPatterns.forEach(pattern => {
      const matches = text.match(pattern);
      if (matches) {
        analysis.explanationCount += matches.length;
      }
    });

    // Detectar indicadores de profesor
    const professorPatterns = [
      /\b(clase|leccin|tema|objetivo|aprendizaje)\b/gi,
      /\b(estudiantes|alumnos|compaeros)\b/gi,
      /\b(entender|comprender|aprender|analizar|evaluar)\b/gi
    ];
    
    professorPatterns.forEach(pattern => {
      const matches = text.match(pattern);
      if (matches) {
        analysis.professorIndicators += matches.length;
      }
    });

    // Detectar indicadores de estudiante
    const studentPatterns = [
      /\b(pregunta|duda|entend|comprend|aprend)\b/gi,
      /\b(no entiendo|no comprendo|no s|confuso)\b/gi,
      /\b(interesante|importante|relevante|til)\b/gi
    ];
    
    studentPatterns.forEach(pattern => {
      const matches = text.match(pattern);
      if (matches) {
        analysis.studentIndicators += matches.length;
      }
    });

    // Anlisis de frecuencia de palabras
    const wordFrequency: Record<string, number> = {};
    words.forEach(word => {
      if (word.length > 3) { // Ignorar palabras muy cortas
        wordFrequency[word] = (wordFrequency[word] || 0) + 1;
      }
    });

    // Ordenar palabras por frecuencia
    analysis.mainWords = Object.entries(wordFrequency)
      .sort(([,a], [,b]) => b - a)
      .slice(0, 10);

    // Detectar temas basados en palabras clave frecuentes
    const topicKeywords = {
      'matemticas': ['nmero', 'clculo', 'ecuacin', 'lgebra', 'geometra'],
      'ciencias': ['experimento', 'investigacin', 'mtodo', 'hiptesis', 'teora'],
      'lenguaje': ['texto', 'lectura', 'escritura', 'gramtica', 'vocabulario'],
      'historia': ['histrico', 'pasado', 'evento', 'periodo', 'civilizacin'],
      'tecnologa': ['digital', 'computadora', 'software', 'hardware', 'programacin']
    };

    Object.entries(topicKeywords).forEach(([topic, keywords]) => {
      const topicMatches = keywords.filter(keyword => 
        text.toLowerCase().includes(keyword.toLowerCase())
      );
      if (topicMatches.length > 0) {
        analysis.topicsDiscussed.push(topic);
      }
    });

    // Si no se detectaron temas, usar las palabras ms frecuentes
    if (analysis.topicsDiscussed.length === 0 && analysis.mainWords.length > 0) {
      analysis.topicsDiscussed = analysis.mainWords
        .slice(0, 3)
        .map(([word]) => word);
    }

    // Detectar ejemplos
    const examplePatterns = [
      /\b(por ejemplo|como ejemplo|un caso|una instancia)\b/gi,
      /\b(imaginemos|supongamos|consideremos)\b/gi
    ];
    
    examplePatterns.forEach(pattern => {
      const matches = text.match(pattern);
      if (matches) {
        analysis.examplesUsed.push(...matches);
      }
    });

    // Si no se detectaron ejemplos, usar algunas palabras clave como ejemplos
    if (analysis.examplesUsed.length === 0 && analysis.mainWords.length > 0) {
      analysis.examplesUsed = analysis.mainWords
        .slice(0, 2)
        .map(([word]) => `Ejemplo de ${word}`);
    }

    analysis.keywordFrequency = wordFrequency;
    return analysis;
  };

  const generateContentBasedSummary = (text: string, analysis: any, recordingTime: number, formatTime: (seconds: number) => string) => {
    if (!text.trim() || analysis.wordCount < 5) {
      return "No se detect contenido suficiente en la transcripcin para generar un resumen detallado. Asegrese de que la grabacin capture audio claro y que haya contenido hablado durante la sesin.";
    }

    // Extraer el ttulo o tema principal
    const firstLine = text.split('\n')[0].trim();
    const title = firstLine.includes('') ? firstLine.split('')[0].trim() : firstLine;

    // Identificar participantes
    const participants = [];
    const participantPattern = /(?:Prof\.|Estudiante)\s+[^:]+/g;
    const matches = text.match(participantPattern) || [];
    matches.forEach(match => {
      if (!participants.includes(match)) {
        participants.push(match);
      }
    });

    // Generar resumen estructurado
    let summary = `RESUMEN EJECUTIVO\n\n`;
    summary += `${title}\n`;
    summary += `Duracin: ${formatTime(recordingTime)} | Contenido: ${analysis.wordCount} palabras\n\n`;

    // Informacin de participantes
    if (participants.length > 0) {
      summary += `PARTICIPANTES:\n`;
      participants.forEach(participant => {
        summary += `   ${participant}\n`;
      });
      summary += '\n';
    }

    // Temas principales
    if (analysis.topicsDiscussed.length > 0) {
      summary += `TEMAS PRINCIPALES:\n`;
      analysis.topicsDiscussed.forEach((topic: string) => {
        summary += `   ${topic}\n`;
      });
      summary += '\n';
    }

    // Puntos clave de la discusin
    const explanationPattern = /\b(es decir|por ejemplo|como|significa|definimos|concepto de|teora de)\b.*?[.!?]/gi;
    const explanations = text.match(explanationPattern) || [];
    if (explanations.length > 0) {
      summary += `PUNTOS CLAVE:\n`;
      explanations.slice(0, 3).forEach((exp: string) => {
        summary += `   ${exp.trim()}\n`;
      });
      summary += '\n';
    }

    // Interacciones significativas
    if (analysis.questionCount > 0) {
      summary += `PREGUNTAS DESTACADAS:\n`;
      // Extraer preguntas del texto
      const questionPattern = /[^.!?]*\?/g;
      const questions = text.match(questionPattern) || [];
      questions.slice(0, 2).forEach((question: string) => {
        summary += `   "${question.trim()}"\n`;
      });
      summary += '\n';
    }

    // Mtricas y participacin
    summary += `MTRICAS DE LA SESIN:\n`;
    if (analysis.examplesUsed.length > 0) {
      summary += `   Estrategias didcticas: ${analysis.examplesUsed.join(', ')}\n`;
    }
    if (analysis.explanationCount > 0) {
      summary += `   Explicaciones realizadas: ${analysis.explanationCount}\n`;
    }
    const totalIndicators = analysis.professorIndicators + analysis.studentIndicators;
    if (totalIndicators > 0) {
      const professorPercentage = Math.round((analysis.professorIndicators / totalIndicators) * 100);
      summary += `   Distribucin de participacin: ${professorPercentage}% docente / ${100 - professorPercentage}% estudiantil\n`;
      if (analysis.questionCount > 0) {
        summary += `   Preguntas formuladas: ${analysis.questionCount}\n`;
      }
    }

    return summary;
  };

  const generateAnalysis = async (transcript: string, recordingTime: number, formatTime: (seconds: number) => string) => {
    if (!transcript.trim()) {
      toast({
        title: "Error",
        description: "Necesitas tener una transcripcin para generar el anlisis",
        variant: "destructive"
      });
      return;
    }

    setIsAnalyzing(true);

    try {
      const analysis = analyzeTranscriptContent(transcript);
      const summary = generateContentBasedSummary(transcript, analysis, recordingTime, formatTime);

      // Generar evaluacin por rbricas basada en el anlisis
      const rubricEvaluation = {
        domainKnowledge: Math.min(5, 2 + (analysis.explanationCount * 0.5) + (analysis.topicsDiscussed.length * 0.3)),
        teachingMethodology: Math.min(5, 2 + (analysis.professorIndicators * 0.1) + (analysis.explanationCount * 0.4)),
        studentEngagement: Math.min(5, 1 + (analysis.questionCount * 0.3) + (analysis.studentIndicators * 0.2)),
        classroomManagement: Math.min(5, 3 + ((analysis.professorIndicators + analysis.studentIndicators) * 0.1)),
        communicationSkills: Math.min(5, 2.5 + (analysis.wordCount > 200 ? 1 : 0) + (analysis.explanationCount * 0.3)),
        comments: [
          `Se identificaron ${analysis.explanationCount} explicaciones detalladas durante la sesin`,
          `La participacin estudiantil registr ${analysis.questionCount} preguntas`,
          `El contenido cubri ${analysis.topicsDiscussed.length} temas principales`,
          analysis.wordCount > 200 ? 'Contenido sustancial registrado' : 'Oportunidad de ampliar el contenido'
        ]
      };

      // Generar observacin estructurada
      const structuredObservation = {
        planningAndPreparation: [
          analysis.topicsDiscussed.length > 2 ? 'Contenido bien estructurado con mltiples temas' : 'Estructura temtica bsica',
          analysis.explanationCount > 0 ? 'Explicaciones preparadas y coherentes' : 'Oportunidad de incluir ms explicaciones'
        ],
        instructionalDelivery: [
          `Se registraron ${analysis.explanationCount} explicaciones durante la clase`,
          analysis.questionCount > 0 ? `${analysis.questionCount} preguntas facilitaron la comprensin` : 'Poca verificacin de comprensin registrada',
          analysis.examplesUsed.length > 0 ? `Uso de ejemplos: ${analysis.examplesUsed.join(', ')}` : 'Oportunidad de incluir ms ejemplos prcticos'
        ],
        classroomEnvironment: [
          analysis.studentIndicators > 0 ? 'Ambiente propicio para la participacin estudiantil' : 'Ambiente principalmente expositivo',
          analysis.questionCount > 2 ? 'Alta interactividad registrada' : 'Oportunidad de aumentar la interactividad'
        ],
        professionalResponsibilities: [
          'Uso de tecnologa para registro y anlisis de la clase',
          analysis.wordCount > 100 ? 'Contenido acadmico sustancial' : 'Contenido bsico registrado'
        ]
      };

      // Generar retroalimentacin 360
      const feedback360 = {
        selfEvaluation: [
          analysis.explanationCount > 2 ? 'Metodologa explicativa efectiva implementada' : 'Oportunidad de enriquecer las explicaciones',
          analysis.topicsDiscussed.length > 1 ? 'Cobertura temtica diversificada lograda' : 'Enfoque temtico concentrado'
        ],
        studentPerspective: [
          analysis.questionCount > 0 ? `Los estudiantes formularon ${analysis.questionCount} preguntas, indicando engagement` : 'Poca participacin estudiantil registrada',
          analysis.studentIndicators > 0 ? 'Indicadores de participacin estudiantil detectados' : 'Oportunidad de estimular ms participacin'
        ],
        peerObservations: [
          analysis.professorIndicators > 2 ? 'Metodologa docente estructurada observable' : 'Metodologa bsica registrada',
          analysis.topicsDiscussed.length > 0 ? 'Uso de anlisis de contenido para mejora continua' : 'Enfoque tradicional de enseanza'
        ],
        improvementAreas: [
          analysis.questionCount < 2 ? 'Incrementar tcnicas de verificacin de comprensin' : 'Mantener el nivel de interactividad',
          analysis.examplesUsed.length === 0 ? 'Incorporar ms ejemplos prcticos y casos de estudio' : 'Continuar con el uso efectivo de ejemplos'
        ]
      };

      // Generar portafolio docente
      const teachingPortfolio = {
        strengths: [
          analysis.explanationCount > 0 ? `Capacidad explicativa: ${analysis.explanationCount} explicaciones registradas` : 'Enfoque directo de enseanza',
          analysis.topicsDiscussed.length > 1 ? `Cobertura temtica amplia: ${analysis.topicsDiscussed.join(', ')}` : 'Enfoque temtico especfico',
          analysis.wordCount > 150 ? 'Riqueza de contenido verbal' : 'Comunicacin concisa'
        ],
        innovativePractices: [
          'Implementacin de anlisis automatizado de clases',
          'Uso de transcripcin para mejora pedaggica',
          analysis.topicsDiscussed.length > 0 ? 'Anlisis de frecuencia de palabras clave' : 'Enfoque tradicional'
        ],
        studentLearningEvidence: [
          analysis.questionCount > 0 ? `${analysis.questionCount} preguntas estudiantiles demuestran engagement` : 'Participacin estudiantil limitada',
          analysis.studentIndicators > 0 ? 'Indicadores de participacin activa registrados' : 'Formato principalmente expositivo'
        ],
        reflectiveInsights: [
          `La clase de ${formatTime(recordingTime)} gener ${analysis.wordCount} palabras de contenido`,
          analysis.mainWords.length > 0 ? `Trminos clave identificados: ${analysis.mainWords.slice(0, 3).map(([word]) => word).join(', ')}` : 'Vocabulario acadmico estndar',
          'El anlisis automtico permite identificar patrones para mejora continua'
        ]
      };

      // Generar evaluacin ECDF
      const ecdfScore = Math.min(4, 1 + (analysis.explanationCount * 0.3) + (analysis.questionCount * 0.2) + (analysis.topicsDiscussed.length * 0.2));
      
      const ecdfModel = {
        domainExpertise: analysis.topicsDiscussed.length > 1 ? 
          `Demuestra conocimiento slido en ${analysis.topicsDiscussed.length} reas temticas: ${analysis.topicsDiscussed.join(', ')}` :
          'Conocimiento bsico del rea temtica evidenciado',
        pedagogicalKnowledge: analysis.explanationCount > 1 ?
          `Aplicacin efectiva de estrategias explicativas (${analysis.explanationCount} explicaciones registradas)` :
          'Metodologa pedaggica bsica implementada',
        contextualKnowledge: analysis.studentIndicators > 0 ?
          'Adaptacin al contexto estudiantil evidenciada en las interacciones registradas' :
          'Oportunidad de mayor adaptacin al contexto estudiantil',
        professionalDevelopment: 'Uso de herramientas tecnolgicas para anlisis y mejora de la prctica docente',
        score: ecdfScore
      };

      // Generar anlisis de participacin estudiantil
      const studentParticipation = analysis.studentIndicators > 0
        ? `Se detectaron ${analysis.studentIndicators} indicadores de participacin estudiantil en la transcripcin. ${
            analysis.questionCount > 0
              ? `Los estudiantes formularon ${analysis.questionCount} preguntas durante la sesin, ${
                  analysis.studentIndicators >= 3
                    ? 'demostrando un ambiente de aprendizaje interactivo donde los estudiantes se sienten cmodos participando.'
                    : 'sugiriendo oportunidades para fomentar mayor participacin activa.'
                }`
              : 'No se registraron preguntas por parte de los estudiantes, indicando la necesidad de implementar estrategias ms dinmicas para estimular el engagement estudiantil.'
          }`
        : 'La transcripcin no muestra indicadores claros de participacin estudiantil directa. Se recomienda implementar estrategias para fomentar la participacin activa.';

      // Generar anlisis de desempeo del profesor
      const professorPerformance = analysis.professorIndicators > 0
        ? `Se identificaron ${analysis.professorIndicators} indicadores de instruccin estructurada en la transcripcin. ${
            analysis.explanationCount > 0
              ? `El profesor proporcion ${analysis.explanationCount} explicaciones detalladas, ${
                  analysis.topicsDiscussed.length > 0
                    ? `cubriendo ${analysis.topicsDiscussed.length} temas principales: ${analysis.topicsDiscussed.join(', ')}. `
                    : 'con cobertura temtica limitada detectada. '
                }`
              : 'No se registraron explicaciones detalladas en la transcripcin, con cobertura temtica limitada detectada. '
          }${
            analysis.professorIndicators >= 3 && analysis.explanationCount >= 2
              ? 'Esto indica un desempeo docente slido con metodologa clara y contenido bien estructurado.'
              : 'Hay oportunidades para enriquecer la metodologa de enseanza y aumentar la claridad en las explicaciones.'
          }`
        : 'El anlisis no detect indicadores claros de metodologa docente estructurada. Se recomienda implementar estrategias ms claras de enseanza.';

      // Generar objetivos basados en el contenido
      const classObjective = {
        mainObjective: analysis.topicsDiscussed.length > 0 
          ? `Comprender los conceptos fundamentales de ${analysis.topicsDiscussed[0]}`
          : 'Desarrollar comprensin de los temas principales de la clase',
        specificObjectives: [
          ...analysis.topicsDiscussed.map(topic => `Entender los conceptos clave de ${topic}`),
          analysis.explanationCount > 0 ? 'Aplicar los conceptos explicados en ejercicios prcticos' : 'Identificar los puntos principales de la explicacin',
          analysis.questionCount > 0 ? 'Participar activamente en la discusin de conceptos' : 'Desarrollar preguntas sobre los temas tratados'
        ],
        achievementStatus: analysis.explanationCount > 2 && analysis.questionCount > 1 
          ? 'achieved' as const
          : analysis.explanationCount > 0 
            ? 'partially_achieved' as const
            : 'not_achieved' as const,
        evidence: [
          analysis.explanationCount > 0 ? `${analysis.explanationCount} explicaciones detalladas de conceptos` : 'Explicaciones bsicas de conceptos',
          analysis.questionCount > 0 ? `${analysis.questionCount} preguntas y respuestas durante la clase` : 'Interaccin limitada durante la clase',
          analysis.topicsDiscussed.length > 0 ? `Cobertura de ${analysis.topicsDiscussed.length} temas principales` : 'Cobertura bsica de temas'
        ],
        recommendations: [
          analysis.explanationCount < 2 ? 'Incluir ms explicaciones detalladas de conceptos clave' : 'Mantener el nivel de explicacin detallada',
          analysis.questionCount < 2 ? 'Fomentar ms preguntas y participacin de los estudiantes' : 'Continuar promoviendo la participacin activa',
          analysis.topicsDiscussed.length < 2 ? 'Ampliar la cobertura de temas relacionados' : 'Mantener la profundidad en los temas tratados'
        ]
      };

      // Actualizar el estado con todos los anlisis
      setClassAnalysis({
        summary,
        rubricEvaluation,
        structuredObservation,
        feedback360,
        teachingPortfolio,
        ecdfModel,
        studentParticipation,
        professorPerformance,
        voiceAnalysis: {
          totalSpeakers: Math.max(1, Math.ceil((analysis.professorIndicators + analysis.studentIndicators) / 2)),
          professorSpeechTime: Math.round((analysis.professorIndicators / (analysis.professorIndicators + analysis.studentIndicators + 1)) * recordingTime),
          studentSpeechTime: Math.round((analysis.studentIndicators / (analysis.professorIndicators + analysis.studentIndicators + 1)) * recordingTime),
          questionCount: analysis.questionCount,
          interactionCount: analysis.professorIndicators + analysis.studentIndicators
        },
        contentAnalysis: {
          topicsDiscussed: analysis.topicsDiscussed,
          conceptsExplained: analysis.mainWords.map(([word]) => word),
          examplesUsed: analysis.examplesUsed,
          keywordFrequency: analysis.keywordFrequency
        },
        classObjective
      });

      toast({
        title: "Anlisis completado",
        description: `Evaluaciones generadas basadas en el contenido real de ${analysis.wordCount} palabras`,
      });
    } catch (error) {
      console.error('Error al generar el anlisis:', error);
      toast({
        title: "Error",
        description: "Hubo un problema al generar el anlisis. Por favor, intenta de nuevo.",
        variant: "destructive"
      });
    } finally {
      setIsAnalyzing(false);
    }
  };

  const resetAnalysis = () => {
    setClassAnalysis({
      summary: '',
      rubricEvaluation: {
        domainKnowledge: 0,
        teachingMethodology: 0,
        studentEngagement: 0,
        classroomManagement: 0,
        communicationSkills: 0,
        comments: []
      },
      structuredObservation: {
        planningAndPreparation: [],
        instructionalDelivery: [],
        classroomEnvironment: [],
        professionalResponsibilities: []
      },
      feedback360: {
        selfEvaluation: [],
        studentPerspective: [],
        peerObservations: [],
        improvementAreas: []
      },
      teachingPortfolio: {
        strengths: [],
        innovativePractices: [],
        studentLearningEvidence: [],
        reflectiveInsights: []
      },
      ecdfModel: {
        domainExpertise: '',
        pedagogicalKnowledge: '',
        contextualKnowledge: '',
        professionalDevelopment: '',
        score: 0
      },
      studentParticipation: '',
      professorPerformance: '',
      voiceAnalysis: {
        totalSpeakers: 0,
        professorSpeechTime: 0,
        studentSpeechTime: 0,
        questionCount: 0,
        interactionCount: 0
      },
      contentAnalysis: {
        topicsDiscussed: [],
        conceptsExplained: [],
        examplesUsed: [],
        keywordFrequency: {}
      },
      classObjective: {
        mainObjective: '',
        specificObjectives: [],
        achievementStatus: 'not_achieved',
        evidence: [],
        recommendations: []
      }
    });
  };

  return {
    classAnalysis,
    isAnalyzing,
    generateAnalysis,
    resetAnalysis
  };
};
